  body {
    font-family: monospace;
    color: white;
    text-align: center;
    margin: 0;
    background: rgb(36,36,36);
    background: -moz-radial-gradient(circle, rgba(36,36,36,1) 0%, rgba(60,60,60,1) 0%, rgba(34,34,34,1) 61%);
    background: -webkit-radial-gradient(circle, rgba(36,36,36,1) 0%, rgba(60,60,60,1) 0%, rgba(34,34,34,1) 61%);
    background: radial-gradient(circle, rgba(36,36,36,1) 0%, rgba(60,60,60,1) 0%, rgba(34,34,34,1) 61%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#242424",endColorstr="#222222",GradientType=1);
  }
  
  a {
    text-decoration: none;
    color: rgb(84, 84, 180);
  }

  #index {
    margin-bottom: 20%;
    margin-top: 10%;
  }

  section {
    margin: 50px;
    padding: 10%;
    margin-left: 3%;
    margin-right: 3%;
    background-color: rgb(24, 24, 24);
    font-size: 20px;
    box-shadow: 0 0 10px rgba(160, 160, 160, 0.125);
    opacity: 0;
    transform: translate(0, 50%);
    visibility: hidden;
    transition: opacity 0.8s ease-out, transform 0.8s ease-out;
    will-change: opacity, visibility;
    flex-direction: column;
    align-items: center;
  }


.bold {
  font-weight: 700;
}

code {
  background-color:rgba(47, 47, 47, 0.95);
}

  img {
    width: 13%;
    height: 13%;
    border-radius: 50%;
    margin-top: 30px;
    margin-bottom: -5%;
  }

  #name {
    display: inline;
    font-size: 50px;
    padding-top: 50px;
  }

  span {
    display: inline;
  }

  #typewriter {
    font-size: 30px;
  }
  
  .is-visible {
    opacity: 1;
    transform: none;
    visibility: visible;
    display: flex;
  }

  @media only screen and (max-width: 700px) {
    img {
      width: 50%;
      height: 50%;
    }
  }

  @media only screen and (max-width: 480px) {
    #name {
      font-size: 35px; 
    }
  }

  @media only screen and (max-width: 375px) {
    #name {
      font-size: 25px; 
    }

    section {
      padding: 10%;
      
    }
  }